/* General */

h1 {
    @apply text-primary-800;
}

.sp-theme-dark h1 {
    @apply text-primary;
}

h2 {
    @apply mb-6 text-2xl font-normal leading-none;
}

h3 {
    @apply my-6 text-xl font-normal leading-none;
}

h4 {
    @apply m-0 text-lg;
}

body > .sp-alert-sticky {
    @apply sticky px-6 mb-0 rounded-none z-50;
    top: 3rem;
}

@screen xs {
    body > .sp-alert-sticky {
        top: 3.5rem;
    }
}

@screen lg {
    body > .sp-alert-sticky {
        @apply px-8;
        top: 4rem;
    }
}

/* Header */

header .sp-container {
    height: 3rem;
}

.sp-logo {
    max-height: 2rem;
}

@screen xs {
    header .sp-container {
        height: 3.5rem;
    }

    .sp-logo {
        max-height: 2.5rem;
    }

    .sp-mobile-nav-button {
        @apply py-1;
    }
}

@screen lg {
    header .sp-container {
        height: 4rem;
    }

    .sp-logo {
        max-height: 3rem;
    }
}

.sp-mobile-nav-button div {
    @apply border-primary-800 text-primary-800;
}

.sp-theme-dark .sp-mobile-nav-button div {
    @apply border-white text-white;
}

.sp-mobile-nav-button div:hover {
    @apply bg-primary-800 text-white;
}

.sp-theme-dark .sp-mobile-nav-button div:hover {
    @apply bg-white text-primary-800;
}

.sp-mobile-nav-button svg {
    @apply fill-current;
    height: 1.375rem;
    width: 1.375rem;
}

.sp-mobile-nav {
    margin-top: 3rem;
    max-height: calc(100% - 3rem);
}

body.sp-mobile-nav-open {
    @apply overflow-hidden;
}

body.sp-mobile-nav-open .sp-content {
    @apply opacity-50;
}

@screen xs {
    .sp-mobile-nav {
        margin-top: 3.5rem;
        max-height: calc(100% - 3.5rem);
    }
}

@screen lg {
    .sp-mobile-nav {
        @apply hidden !important;
    }

    body.sp-mobile-nav-open {
        @apply overflow-auto;
    }

    body.sp-mobile-nav-open .sp-content {
        @apply opacity-100;
    }
}

.sp-search form {
    @apply relative w-full;
}

.sp-search form:before {
    @apply absolute text-primary font-bold;
    content: "\f002";
    font-family: var(--font-icon);
    left: 0.75rem;
    top: 0.5rem;
}

.sp-search input {
    @apply w-full m-0;
    padding-left: 2.25rem;
}

/* Navigation */

ul.sp-nav,
ul.sp-nav ul {
    @apply list-none;
}

ul.sp-nav li {
    @apply whitespace-no-wrap;
}

ul.sp-nav li a {
    @apply block py-3/2 text-primary-200 text-lg;
}

ul.sp-nav li a:hover {
    @apply text-white;
}

ul.sp-nav li a.current {
    @apply text-white font-bold;
}

ul.sp-nav ul li a {
    @apply pl-5;
}

ul.sp-nav .nav-item {
    @apply truncate;
}

@screen lg {
    ul.sp-nav li a {
        @apply text-base;
    }
}

/* Breadcrumb */

.sp-breadcrumb,
.sp-breadcrumb a {
    @apply text-secondary;
}

/* Sidebar */

.sp-sidebar .sp-sidebar-box + .sp-sidebar-box {
    @apply pt-8 mt-8 border-t border-tertiary;
}

.sp-sidebar h2 {
    @apply text-secondary;
}

.sp-sidebar ul {
    @apply p-0 m-0;
}

.sp-sidebar ul li {
    @apply py-1/2;
}

.sp-sidebar ul,
.sp-sidebar ul.sp-nav li {
    @apply p-0;
}

.sp-sidebar ul.sp-nav li a {
    @apply px-3 text-primary truncate;
}

.sp-sidebar ul.sp-nav ul li a {
    @apply pl-8;
}

.sp-sidebar ul.sp-nav li a:hover {
    @apply text-secondary;
}

.sp-sidebar ul.sp-nav li a.current {
    @apply bg-primary border-r-4 border-primary-800 rounded shadow text-primary;
}

.sp-sidebar ul.sp-nav li a.current:hover {
    @apply text-primary;
}

.sp-sidebar input[type="checkbox"],
.sp-sidebar input[type="email"],
.sp-sidebar input[type="number"],
.sp-sidebar input[type="password"],
.sp-sidebar input[type="radio"],
.sp-sidebar input[type="search"],
.sp-sidebar input[type="tel"],
.sp-sidebar input[type="text"],
.sp-sidebar input[type="url"],
.sp-sidebar select,
.sp-sidebar textarea,
.sp-sidebar .sp-form-customfields .redactor-in,
.sp-sidebar .sp-form-customfields .redactor-read-only {
    @apply bg-primary border-primary shadow;
}

.sp-sidebar input[type="email"],
.sp-sidebar input[type="number"],
.sp-sidebar input[type="password"],
.sp-sidebar input[type="search"],
.sp-sidebar input[type="tel"],
.sp-sidebar input[type="text"],
.sp-sidebar input[type="url"] {
    @apply w-full;
}

.sp-sidebar input[type="checkbox"],
.sp-sidebar input[type="radio"] {
    @apply bg-primary border-primary;
}

.sp-sidebar input[type="checkbox"]::before,
.sp-sidebar input[type="radio"]:before {
    @apply border-primary;
}

.sp-sidebar input[type="checkbox"]:hover::before,
.sp-sidebar input[type="radio"]:hover::before {
    @apply bg-primary;
}
.sp-sidebar input[type="radio"]:checked::before,
.sp-sidebar input[type="radio"]:checked:hover::before {
    @apply bg-green-600;
}

.sp-sidebar select {
    @apply max-w-full;
}

.sp-sidebar .sp-form-row {
    @apply mb-4;
}

.sp-sidebar .sp-form-row:last-child {
    @apply mb-0;
}

.sp-sidebar .sp-form-row label {
    @apply text-secondary text-sm;
}

.sp-sidebar .sp-form-button {
    @apply ml-0;
}

.sp-sidebar .sp-collapsible {
    @apply bg-tertiary;
}

@screen lg {
    .sp-sidebar .sp-collapsible.lg\:sp-hidden + .sp-hidden {
        @apply block !important;
    }
}

/* Dashboard */

.sp-dashboard-section {
    @apply p-6 pt-0 -mx-6 mb-6 border-b border-tertiary;
}

.sp-dashboard-section:last-child {
    @apply pb-0 mb-0 border-0;
}

.sp-dashboard-section .sp-collapsible {
    @apply p-0 bg-transparent rounded-none shadow-none font-normal;
}

.sp-dashboard-section .sp-icon .fas {
    @apply text-primary-800;
}

.sp-theme-dark .sp-dashboard-section .sp-icon .fas {
    @apply text-primary;
}

/* Ticket View */

.sp-ticket-details .sp-form-row {
    @apply mb-3
}

.sp-ticket-details .sp-form-row label {
    @apply mb-0
}

.sp-recipients {
    @apply border border-b-0 border-tertiary rounded-t;
}

.sp-recipients div.sp-table-row > div.sp-table-cell {
    @apply w-full px-2 py-3/2 align-middle;
}

.sp-recipients div.sp-table-row > div.sp-table-cell:first-child {
    @apply w-auto pr-0 text-secondary text-right whitespace-no-wrap;
}

.sp-recipients .sp-recipients-text {
    @apply whitespace-normal;
}

.sp-recipients .sp-recipients-input .selectize-input {
    @apply min-w-0 whitespace-normal;
}

/* Comments */

.sp-comment {
    @apply p-4;
}

.sp-comment .sp-comment {
    @apply border-0 p-0;
}

.sp-comment .sp-comment:first-child {
    @apply mt-3;
}

/* Social Login */

a.sp-social-login {
    @apply relative inline-block py-1 rounded-full text-white cursor-pointer;
}

a.sp-social-login:hover {
    @apply text-white opacity-75 cursor-pointer;
}

/* Pagination */

.pagination,
.pager {
    @apply list-none pl-0 mt-6 text-center;
}

.pagination li,
.pager li {
    @apply inline;
}

.pagination li > a,
.pager li > a,
.pagination li > span,
.pager li > span {
    @apply inline-block px-3 py-1 border border-secondary rounded text-lg;
}

.pagination li > a:hover,
.pager li > a:hover,
.pagination li.active > span,
.pager li.active > span {
    @apply bg-secondary;
}

.pagination li.disabled > span,
.pager li.disabled > span {
    @apply opacity-50;
}
